export type Room = {
  id: string;
  name: string;
  building: string;
  floor: string;
  inClass: boolean;

  description?: string;
  courseName?: string;
  lecturerName?: string;
  nextClass?: string;

  currentTemperature?: number;
  targetTemperature?: number;
  currentIlluminance?: number;
  targetLux?: number;
  currentEnergyKWh?: number;
};



const BASE_URL =
  "https://greentwiniotcentraltrigger-ezgmgugyb9fkfwem.japaneast-01.azurewebsites.net/api";

const KEY =
  "XcGh9Rjnkz-NiNrgK6_qD4_slZugmc38Qob2svJAcFEJAzFuoBUalg==";

const DEVICES_KEY =
  "uKQLG3SulECzSU_jJPLsNBoVPG889Qy5IaaxNmiJ5G9QAzFuPH_SrQ==";


export async function getRooms() {
  const res = await fetch(`${BASE_URL}/rooms?code=${KEY}`, {
    cache: "no-store",
  });

  if (!res.ok) {
    console.error("Failed to load rooms", res.status, await res.text());
    throw new Error("Failed to load rooms");
  }

  return res.json();
}


