"use client"

import { useState } from "react"
import { Wind, Lightbulb, Thermometer, Droplets, Activity, Zap } from "lucide-react"
import ACDetailModal from "@/components/device-details/ac-detail-modal"
import LightDetailModal from "@/components/device-details/light-detail-modal"

interface DevicesTabProps {
  room: any
}

const mockDevices = [
  {
    id: "ac-1",
    type: "ac",
    name: "AC Unit",
    model: "Daikin ACA001",
    status: "online",
    powerState: true,
    mode: "cool",
    fanSpeed: "auto",
    targetTemperature: 24,
  },
  {
    id: "light-1",
    type: "light",
    name: "Ceiling Lights",
    model: "Philips Hue",
    status: "online",
    powerState: true,
    brightness: 80,
    currentLux: 450,
    targetLux: 500,
  },
  {
    id: "temp-sensor",
    type: "sensor",
    sensorType: "temperature",
    name: "Temperature Sensor",
    model: "DHT22",
    status: "online",
    value: 22.5,
  },
  {
    id: "humidity-sensor",
    type: "sensor",
    sensorType: "humidity",
    name: "Humidity Sensor",
    model: "DHT22",
    status: "online",
    value: 55,
  },
  {
    id: "motion-sensor",
    type: "sensor",
    sensorType: "motion",
    name: "Motion Sensor",
    model: "PIR HC-SR501",
    status: "online",
    value: true,
  },
  {
    id: "energy-meter",
    type: "sensor",
    sensorType: "energy",
    name: "Energy Meter",
    model: "SCT-013",
    status: "online",
    value: 2.3,
  },
]

export default function DevicesTab({ room }: DevicesTabProps) {
  const [selectedDevice, setSelectedDevice] = useState<any>(null)

  const getDeviceIcon = (type: string, sensorType?: string) => {
    switch (type) {
      case "ac":
        return <Wind size={22} />
      case "light":
        return <Lightbulb size={22} />
      case "sensor":
        switch (sensorType) {
          case "temperature":
            return <Thermometer size={22} />
          case "humidity":
            return <Droplets size={22} />
          case "motion":
            return <Activity size={22} />
          case "energy":
            return <Zap size={22} />
          default:
            return <Activity size={22} />
        }
      default:
        return <Zap size={22} />
    }
  }

  const getIconBgColor = (type: string, sensorType?: string) => {
    if (type === "ac") return "bg-blue-50 text-blue-600"
    if (type === "light") return "bg-yellow-50 text-yellow-600"
    if (type === "sensor") {
      switch (sensorType) {
        case "temperature":
          return "bg-orange-50 text-orange-600"
        case "humidity":
          return "bg-cyan-50 text-cyan-600"
        case "motion":
          return "bg-purple-50 text-purple-600"
        case "energy":
          return "bg-amber-50 text-amber-600"
        default:
          return "bg-gray-50 text-gray-600"
      }
    }
    return "bg-gray-50 text-gray-600"
  }

  const controllableDevices = mockDevices.filter((d) => d.type === "ac" || d.type === "light")
  const sensors = mockDevices.filter((d) => d.type === "sensor")

  return (
    <div className="space-y-6">
      <div>
        <h3 className="text-sm font-semibold text-gray-600 uppercase tracking-wide mb-3 px-1">Controllable Devices</h3>
        <div className="space-y-3">
          {controllableDevices.map((device) => (
            <button
              key={device.id}
              onClick={() => setSelectedDevice(device)}
              className="bg-white rounded-2xl p-5 w-full text-left hover:shadow-md transition-all border border-gray-100 hover:border-green-200"
            >
              <div className="flex items-start justify-between mb-4">
                <div className="flex items-start gap-4 flex-1">
                  <div
                    className={`w-12 h-12 rounded-xl flex items-center justify-center ${getIconBgColor(device.type)}`}
                  >
                    {getDeviceIcon(device.type)}
                  </div>
                  <div className="flex-1">
                    <h4 className="font-semibold text-gray-900 mb-1">{device.name}</h4>
                    <p className="text-sm text-gray-600">{device.model}</p>
                  </div>
                </div>
                <span className="px-3 py-1 rounded-full bg-green-50 text-green-700 text-xs font-medium">
                  {device.status}
                </span>
              </div>

              {device.type === "ac" && (
                <div className="grid grid-cols-3 gap-3 pt-3 border-t border-gray-100">
                  <div>
                    <p className="text-xs text-gray-600 mb-1">Power</p>
                    <p className="font-semibold text-gray-900">{device.powerState ? "ON" : "OFF"}</p>
                  </div>
                  <div>
                    <p className="text-xs text-gray-600 mb-1">Mode</p>
                    <p className="font-semibold text-gray-900 capitalize">{device.mode}</p>
                  </div>
                  <div>
                    <p className="text-xs text-gray-600 mb-1">Target</p>
                    <p className="font-semibold text-gray-900">{device.targetTemperature}°C</p>
                  </div>
                </div>
              )}

              {device.type === "light" && (
                <div className="grid grid-cols-2 gap-3 pt-3 border-t border-gray-100">
                  <div>
                    <p className="text-xs text-gray-600 mb-1">Brightness</p>
                    <p className="font-semibold text-gray-900">{device.brightness}%</p>
                  </div>
                  <div>
                    <p className="text-xs text-gray-600 mb-1">Illuminance</p>
                    <p className="font-semibold text-gray-900">{device.currentLux} lux</p>
                  </div>
                </div>
              )}
            </button>
          ))}
        </div>
      </div>

      <div>
        <h3 className="text-sm font-semibold text-gray-600 uppercase tracking-wide mb-3 px-1">Sensors (Read-only)</h3>
        <div className="grid grid-cols-2 gap-3">
          {sensors.map((device) => (
            <div key={device.id} className="bg-white rounded-2xl p-4 border border-gray-100">
              <div className="flex items-center gap-3 mb-3">
                <div
                  className={`w-10 h-10 rounded-xl flex items-center justify-center ${getIconBgColor(device.type, device.sensorType)}`}
                >
                  {getDeviceIcon(device.type, device.sensorType)}
                </div>
              </div>
              <h4 className="font-semibold text-gray-900 text-sm mb-1">
                {device.sensorType?.charAt(0).toUpperCase()}
                {device.sensorType?.slice(1)}
              </h4>
              <p className="text-2xl font-bold text-gray-900">
                {device.sensorType === "motion"
                  ? device.value
                    ? "Detected"
                    : "Clear"
                  : `${device.value}${device.sensorType === "humidity" ? "%" : device.sensorType === "energy" ? " kWh" : "°C"}`}
              </p>
            </div>
          ))}
        </div>
      </div>

      {/* Device Detail Modals */}
      {selectedDevice?.type === "ac" && (
        <ACDetailModal device={selectedDevice} onClose={() => setSelectedDevice(null)} />
      )}
      {selectedDevice?.type === "light" && (
        <LightDetailModal device={selectedDevice} onClose={() => setSelectedDevice(null)} />
      )}
    </div>
  )
}
